<template>
  <main class="proof">
    <header class="code" :class="{not:status=='REJECT'||status=='FINISHED'}">
      <h3></h3>
      <h2 v-if="status=='REJECT'"></h2>
      <h2 v-else-if="status=='FINISHED'"></h2>
      <h2 v-else-if="status=='READING'"></h2>
      <h2 v-else>{{code}}</h2>
      <h4>  {{time}}</h4>
    </header>
    <Scroll class="proof-detail">
      <div>
        <h3></h3>
        <BookList :lists="lists"></BookList>
        <p class="notice">
          <br>
          1.2<br>
          2.1<br>
          3.<br>
          4.<br>
          <br>
          24 <br>
          5.
        </p>
      </div>
    </Scroll>
  </main>
</template>

<script>
  import BookList from 'base/bookList'
  import Scroll from 'base/scroll'
  export default {
    name: "proof",
    components:{BookList,Scroll},
    data() {
      return {
        code:'------',
        time:'00/00 00:00',
        status:'APPLYING',
        list:[]
      }
    },
    computed:{
      lists(){
        let arr=[]
        this.list.forEach(v=>{
          arr.push(v.book)
        })
        return arr
      }
    },
    activated() {
      let id = this.$route.query.id;
      this.$http.get('/bill/share-show', {params: {id: id, origin: 'LIBRARY'}}).then(r => {
        if (r.status == 'success') {
          this.code = r.data.info.random_code;
          this.time=r.data.info.deadline;
          this.status=r.data.info.status;
          this.list=r.data.info.borrow_library_book_item
        }
      })
    }
  }
</script>

<style scoped lang="scss">
  @import "common/scss/const.scss";
  @import "common/scss/mymixin.scss";

  .proof {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 4;
    background: $color-background;
  }
  .code{
    height: 322px;
    margin: 20px 24px;
    padding: 40px 0 50px;
    box-sizing: border-box;
    background-image: url("../common/img/library_jsm_selected.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: #fff;
    @extend %center;
    flex-direction: column;
    &.not{
      background-image: url("../common/img/library_jsm_default.png");
      color: $color-text-l;
    }
    h3{
      font-size: $font-size-medium-x;
    }
    h2{
      font-size: 96px;
    }
    h4{
      font-size: $font-size-medium;
    }
  }
  .proof-detail{
    @extend %cover;
    top: 362px;
    overflow: hidden;
    h3{
      line-height: 2;
      text-align: left;
      padding-left: 24px;
    }
  }
  .notice{
    padding: 0 24px;
    font-size: $font-size-medium;
    color: $color-text-l;
    text-align: left;
  }
</style>



